<template>
  <div class="section" id="intentions_section">
    <div class="container-fluid">
      <div class="row">
        <div class="col-xl-3"></div>
        <div class="col-xl-6 col-12">
          <transition appear name="rotate">
            <p class="h5 align-right"><span class="intention-word-right">culturelles</span></p>
          </transition>
            <p class="justif">
              Nous sommes convaincus de lutilit des projets  vocation culturelle. Aujourdhui peut-tre plus encore, des projets inventifs, qui soient  la fois plus pragmatiques et plus ambitieux. Caf conoit, avec les porteurs de ceux-ci, des projets qui font sens.
            </p>
            <p class="h5 text-center"><span class="intention-word-center">d'architectures</span></p>
            <p class="justif">
              Nous sommes galement passionns darchitecture. O larchitecture est vue comme un art de commande, la qualit de lune tant conditionne par celle de lautre. Caf installe, auprs des commanditaires, les conditions dune architecture crative et juste.
            </p>
            <p class="h5 align-left"><span class="intention-word-left">programmation</span></p>
            <p class="justif">
              Nous ne nous reconnaissons que relativement sous le terme de programmation, tel quil dsigne notre mtier. Il y a une part illusoire dans son ambition catgorique. Caf tudie la relation dialectique - cest elle qui fera vritablement projet - entre des usages volutifs et des espaces structurants.
            </p>
        </div>
        <div class="col-xl-3"></div>
      </div>
    </div>
  </div>
</template>

<script>
/* Imports */
import RiseLoader from 'vue-spinner/src/RiseLoader.vue'
/* import { loadProgressBar } from 'axios-progress-bar'
import 'axios-progress-bar/dist/nprogress.css' */

/* data, methods, components... declaration */
export default {
  data () {
    return {
      moduleTitle: 'Intentions'
    }
  },
  title () {
    return `Caf Programmation  ${this.moduleTitle}`
  },
  methods: {
  },
  components: {
    'rise-loader': RiseLoader
  },
  mounted () {
  }
}
</script>

<!-- scoped styles for this component -->
<style scoped>
  @import url('https://fonts.googleapis.com/css?family=Oxygen');
  @import url('https://fonts.googleapis.com/css?family=Raleway');

</style>



